.product_card {
    overflow: hidden;
    margin: 0 10px;
    
}

.product_card_img {
    position: relative;
    height: 400px;
}

.product_card_img:before {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0; 
    z-index: 2;
    transition: all 0.5s ease;
    background-color: rgba(60, 60, 60, 0.2);
    content: "";
    opacity: 0;
}

.product_card_img img {
    height: 100%;
    width: 100%;
}

.product_buttons_box {
    position: absolute;
    top: 62%;
    z-index: 3;
    right: 1px;
}

.product_buttons_box li {
    transition: all 0.3s ease-in-out 0.2s;

    -webkit-transform: translateX(25px);
    transform: translate(20px);
    opacity: 0;
}



.product_buttons_btn li {
